import React from 'react';
import {Link} from 'react-router-dom';

import Badge from '../components/Badge';
import DeleteBadgeModal from '../components/DeleteBadgeModal';

import './styles/BadgeDetails.css';
import logo from '../images/logo.svg';

function BadgeDetails(props) {
    const badge = props.badge;
    
    return (
        
            <div>
                <div className="BadgeDetails__hero">
                    <div className="container">
                        <div className="row">
                            <div className="col-6">
                                <img className="BadgeDetails__hero-logo" src={logo} alt="logo" />
                            </div>
                            <div className="col-6 BadgeDetails__hero-attendant-name">
                                <h1>{badge.firstName} {badge.lastName}</h1>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="BadgeDetails__body center container">
                    <div className="row">
                        <div className="col-2 back">
                            <Link to="/badges" className="btn btn-primary">Badges</Link>
                        </div>
                        <div className="col-8">
                            <div className="BadgeDetails__Badge">
                                <Badge 
                                    firstName={badge.firstName}
                                    lastName={badge.lastName}
                                    jobTitle={badge.jobTitle}
                                    email={badge.email}
                                    tel={badge.tel}
                                />
                            </div>
                        </div>
                        <div className="col-2">
                            <h2>Actions</h2>
                            <div>
                                <div>
                                    <Link className="btn btn-primary mb-4" to={`/badge/${badge.id}/edit`}>Edit</Link>
                                </div>
                                <div>
                                    <button onClick={props.onOpenModal}  className="btn btn-danger">Delete</button>
                                    <DeleteBadgeModal 
                                        modalIsOpen={props.modalIsOpen} 
                                        onClose={props.onCloseModal}
                                        onDeleteBadge={props.onDeleteBadge}

                                    />
                                        
                                    
                                    
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        
    );
}

export default BadgeDetails;


