import React from'react'
import {Link} from 'react-router-dom';

import Gravatar from './Gravatar'

import'./styles/BadgesList.css'

function useSearchBadges(badges) {
   const [query, setQuery] = React.useState('');
   const [filteredBadges, setFilteredBadges] = React.useState(badges);

   React.useMemo(
      () => {
         const result = badges.filter(badge => {
         return `${badge.firstName} ${badge.lastName}`.toLowerCase().includes(query.toLowerCase());
         });
         setFilteredBadges(result);
      }, [query, badges]);

   return {query, setQuery, filteredBadges}
}

function BadgesList(props) {

   const badges = props.badges;

   const {query, setQuery, filteredBadges} = useSearchBadges(badges);

   
   
   if (!filteredBadges.length) {
      return (
         <div>
            <div className="form-group mb-4">
               <label htmlFor="">Filter Badges</label>
               <input type="text" className="form-control"
                  value={query}
                  onChange={e => {
                     setQuery(e.target.value)
                  }}
               />
            </div>
            <div>
               <h3>No Badges were found.</h3>
            </div>
         </div>
      )
   }

   return (
      <div>
         <div className="form-group mb-4">
            <label htmlFor="">Filter Badges</label>
            <input type="text" className="form-control"
               value={query}
               onChange={e => {
                  setQuery(e.target.value)
               }}
            />
         </div>
         <ul className="list-unstyled BadgesList">
            {filteredBadges.map((badge)=>{
               return(
                  <li key={badge.id} className="">
                     <Link to={`/badge/${badge.id}`} className="text-reset text-decoration-none BadgesListItem">

                     <Gravatar email={badge.email} alt="" className="BadgesListItem__avatar"/>
                        <div>
                           <div className="name"><strong>{badge.firstName} {badge.lastName}</strong></div>
                           <div className="tel">
                              {badge.tel}
                           </div>
                           <div className="email">
                              <p>{badge.email}</p>
                           </div>
                           <div>{badge.jobTitle}</div>
                        </div>
                     </Link>
                  </li>
               )
            })}
         </ul>
      </div>
   )
   
}

export default BadgesList


