import React from 'react';
import {Link} from 'react-router-dom';

import talking from '../images/talking.svg';
import logo from '../images/logo.svg'

import './styles/Home.css';

function Home() {
    return (
        <div className="Home">
            <div className="col-text">
                <div className="col-text-info">
                    <img className="col-text-info__logo" src={logo} alt="logo" />
                    <h1 className="col-text-info__title">CONTACT MANAGMENT SYSTEM</h1>
                    <p>Keep in touch</p>
                    <Link to="/badges" className="btn btn-primary">Get Started!</Link>
                </div>
                
            </div>
            <div className="col-img">   
                <img className="Home__talking" src={talking} alt="" />
            </div>
        </div>
    );
}

export default Home;


