import React from 'react';

import Modal from './Modal';

import './styles/DeleteBadgeModal.css';

function DeleteBadgeModal(props) {
    return (
        <Modal modalIsOpen={props.modalIsOpen} onClose={props.onCloseModal}>
            <div className="DeleteBadgeModal">
                <h1>Are you sure?</h1>
                <p>You are about to delte this badge.</p>

                <div className="DeleteBadgeModal__buttons">
                    <button onClick={props.onDeleteBadge} className="btn btn-danger">Delete</button>
                    <button onClick={props.onClose} className="btn btn-primary ml-5">Cancel</button>
                </div>
            </div>
        </Modal>
    );
}

export default DeleteBadgeModal;


